
















import NumberPad from "@/components/money/NumberPad.vue";
import FormItem from "@/components/money/FormItem.vue";
import Tags from "@/components/money/Tags.vue";
import Tabs from "@/components/Tabs.vue";
import Vue from "vue";
import {Component} from "vue-property-decorator";
import recordTypeList from "@/constant/recordTypeList";
import dayjs from "dayjs";
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
import 'vue2-datepicker/locale/zh-cn';

@Component({
  components: {NumberPad, FormItem, Tags, Tabs, DatePicker},
})
export default class Money extends Vue {
  editable = false;//
  type = '-';
  dateX = dayjs(new Date().toISOString()).format('M-D');// +new Date() + 8 * 3600 * 1000
  record: RecordItem = {
    tags: {"id": "1", "name": "", "svg": "expenditure", "type": "-"},
    formItem: "",
    type: "-",
    amount: 0,
    createdAt: new Date().toISOString(),//
  }

  di(date: Date) {
    let hour = date.getHours() + 8;
    date.setHours(hour);//
    const x = dayjs(date.toISOString()).format('YYYY') === dayjs(new Date()).format('YYYY')
    x ? this.dateX = dayjs(date.toISOString()).format('MD') : this.dateX = dayjs(date.toISOString()).format('YYYYMD')
    this.record.createdAt = date.toISOString();
  }

  saveRecord() {
    this.$store.commit('createRecord', this.record);
    this.record.formItem = "";
  }

  recordTypeList = recordTypeList;

  created() {
    this.$store.commit('fetchRecords')
  }

  onUpdateTabs(type:string){
    this.record.type = type
  }
  get recordList() {
    return this.$store.state.recordList;
  }
  beforeCreate(){
    const tagList = JSON.parse(window.localStorage.getItem('tagsList') || '[]');
    if (tagList && tagList.length === 0) {
      window.localStorage.setItem('tagsList','[\n' +
          '{"id":"1","name":"","svg":"expenditure","type":"-"},\n' +
          '{"id":"2","name":"","svg":"income","type":"+"},\n' +
          '{"id":"3","name":"","svg":"transfer","type":"+"},\n' +
          '{"id":"4","name":"","svg":"redPackets","type":"+"},\n' +
          '{"id":"5","name":"","svg":"pet","type":"-"},\n' +
          '{"id":"6","name":"","svg":"hotel","type":"-"},\n' +
          '{"id":"7","name":"","svg":"medical","type":"-"},\n' +
          '{"id":"8","name":"","svg":"fund","type":"+"},\n' +
          '{"id":"9","name":"","svg":"apparel","type":"-"},\n' +
          '{"id":"10","name":"","svg":"entertainment","type":"-"},\n' +
          '{"id":"11","name":"","svg":"livingExpenses","type":"-"},\n' +
          '{"id":"12","name":"","svg":"snack","type":"-"},\n' +
          '{"id":"13","name":"","svg":"commute","type":"-"},\n' +
          '{"id":"14","name":"","svg":"fruit","type":"-"},\n' +
          '{"id":"15","name":"","svg":"food","type":"-"},\n' +
          '{"id":"16","name":"","svg":"repayment","type":"-"},\n' +
          '{"id":"17","name":"","svg":"rent","type":"-"},\n' +
          '{"id":"18","name":"","svg":"cigarette","type":"-"},\n' +
          '{"id":"19","name":"","svg":"wage","type":"+"}\n' +
          ']')
    }
  }
}
